.header {
  max-width: 880px;
  margin: 0 auto 44px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1000px) {
  .header {
  margin: 45px 40px 44px;
  }
}
@media screen and (max-width: 320px) {
  .header {
    max-width: 320px;
    margin: 27px 0 44px;
    padding-left: 28px;
  }
}